import React from "react";
import CourseCard from "app/views/common/components/CourseCard";
import "./courses.scss";

function CoursesComponent(props) {
  const {
    courses: { isLoading, error, errMsg, data },
    openCourseDetails,
  } = props;

  if (error) return <div>{errMsg}</div>;
  else if (isLoading) return <div>Loading data...</div>;
  else
    return (
      <div id="cfg_courses_page" className="page">
        {data.map((courseInfo) => (
          <div key={`${courseInfo.id}`}>
            <CourseCard {...courseInfo} openCourseDetails={openCourseDetails} />
            <div className="course-seperator"></div>
          </div>
        ))}
      </div>
    );
}

export default CoursesComponent;
