import React, { useEffect } from "react";
import CourseComponent from "./CoursesComponent";
import { coursesOperations } from "app/store/state/courses";
import { useDispatch, useSelector } from "react-redux";

function Courses(props) {
  const dispatch = useDispatch();
  const courses = useSelector((state) => state.courses);
  const openCourseDetails = (courseId) => {
    props.history.push(`/course/${courseId}`);
  };

  useEffect(() => {
    dispatch(coursesOperations.loadCourses());
    // eslint-disable-next-line
  }, []);

  return (
    <CourseComponent courses={courses} openCourseDetails={openCourseDetails} />
  );
}

export default Courses;
